import React, { useState } from 'react';

function AnalysisPage() {
  const [selectedTab, setSelectedTab] = useState('day'); //   
  const [data, setData] = useState([
    { id: 1, date: '2024-11-19', temperature: 25, humidity: 60, soilMoisture: 30 },
    { id: 2, date: '2024-11-18', temperature: 22, humidity: 65, soilMoisture: 35 },
    { id: 3, date: '2024-11-17', temperature: 24, humidity: 55, soilMoisture: 40 },
  ]); //  

  //   
  const handleAddData = () => {
    const date = prompt('   (YYYY-MM-DD):');
    const temperature = parseFloat(prompt('  :'));
    const humidity = parseFloat(prompt('  :'));
    const soilMoisture = parseFloat(prompt('   :'));

    if (date && temperature && humidity && soilMoisture) {
      const newData = {
        id: data.length + 1,
        date,
        temperature,
        humidity,
        soilMoisture,
      };
      setData([...data, newData]);
    } else {
      alert('   .');
    }
  };

  //     
  const filteredData =
    selectedTab === 'day'
      ? data.slice(0, 1) //   
      : selectedTab === 'week'
      ? data.slice(0, 7) //  7 
      : data; //   ( )

  return (
    <div style={{ textAlign: 'center', marginTop: '30px' }}>
      <h2>  </h2>
      <div style={{ marginBottom: '20px' }}>
        <button
          style={{
            margin: '5px',
            padding: '10px 20px',
            backgroundColor: selectedTab === 'day' ? '#4caf50' : '#ccc',
            color: '#fff',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
          }}
          onClick={() => setSelectedTab('day')}
        >
           
        </button>
        <button
          style={{
            margin: '5px',
            padding: '10px 20px',
            backgroundColor: selectedTab === 'week' ? '#4caf50' : '#ccc',
            color: '#fff',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
          }}
          onClick={() => setSelectedTab('week')}
        >
           
        </button>
        <button
          style={{
            margin: '5px',
            padding: '10px 20px',
            backgroundColor: selectedTab === 'month' ? '#4caf50' : '#ccc',
            color: '#fff',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
          }}
          onClick={() => setSelectedTab('month')}
        >
           
        </button>
      </div>

      <div>
        <h3> </h3>
        <button
          style={{
            margin: '10px',
            padding: '10px 20px',
            backgroundColor: '#007bff',
            color: '#fff',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
          }}
          onClick={handleAddData}
        >
           
        </button>
        <table style={{ margin: '20px auto', width: '80%', borderCollapse: 'collapse' }}>
          <thead>
            <tr style={{ backgroundColor: '#f4f4f4' }}>
              <th style={{ padding: '10px', border: '1px solid #ddd' }}></th>
              <th style={{ padding: '10px', border: '1px solid #ddd' }}> (C)</th>
              <th style={{ padding: '10px', border: '1px solid #ddd' }}> (%)</th>
              <th style={{ padding: '10px', border: '1px solid #ddd' }}>  (%)</th>
            </tr>
          </thead>
          <tbody>
            {filteredData.map((entry) => (
              <tr key={entry.id}>
                <td style={{ padding: '10px', border: '1px solid #ddd' }}>{entry.date}</td>
                <td style={{ padding: '10px', border: '1px solid #ddd' }}>{entry.temperature}</td>
                <td style={{ padding: '10px', border: '1px solid #ddd' }}>{entry.humidity}</td>
                <td style={{ padding: '10px', border: '1px solid #ddd' }}>{entry.soilMoisture}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default AnalysisPage;
