import React, { useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';

function ChartComponent() {
  const chartRef = useRef(null);
  const canvasRef = useRef(null);
  const MAX_VISIBLE_POINTS = 20;

  useEffect(() => {
    const ctx = canvasRef.current.getContext('2d');
    const chartInstance = new Chart(ctx, {
      type: 'line',
      data: {
        labels: [],
        datasets: [
          {
            label: '',
            data: [],
            borderColor: 'rgba(255, 99, 132, 1)',
            backgroundColor: 'rgba(255, 99, 132, 0.2)',
            borderWidth: 1,
          },
          {
            label: '',
            data: [],
            borderColor: 'rgba(54, 162, 235, 1)',
            backgroundColor: 'rgba(54, 162, 235, 0.2)',
            borderWidth: 1,
          },
          {
            label: ' ',
            data: [],
            borderColor: 'rgba(75, 192, 192, 1)',
            backgroundColor: 'rgba(75, 192, 192, 0.2)',
            borderWidth: 1,
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        animation: {
          duration: 500, //    ()
          easing: 'linear', //  
        },
        scales: {
          x: {
            beginAtZero: true,
          },
          y: {
            beginAtZero: true,
          },
        },
      },
    });

    chartRef.current = chartInstance;

    //   
    const interval = setInterval(() => {
      if (chartRef.current) {
        const now = new Date().toLocaleTimeString();
        const { labels, datasets } = chartRef.current.data;

        //   
        labels.push(now);
        datasets[0].data.push(Math.random() * 30 + 20); //  
        datasets[1].data.push(Math.random() * 50 + 30); //  
        datasets[2].data.push(Math.random() * 100); //   

        //     
        if (labels.length > MAX_VISIBLE_POINTS) {
          labels.shift(); //    
          datasets.forEach((dataset) => dataset.data.shift()); //    
        }

        chartRef.current.update(); //  
      }
    }, 1000); // 1 

    return () => {
      clearInterval(interval);
      if (chartRef.current) {
        chartRef.current.destroy();
      }
    };
  }, []);

  return (
    <div className="chart-container">
      <canvas ref={canvasRef}></canvas>
    </div>
  );
}

export default ChartComponent;
