import React from 'react';

function Controls() {
  const setTemperature = () => {
    const tempValue = prompt("  :");
    if (tempValue) {
      alert(` : ${tempValue}`);
    }
  };

  const setHumidity = () => {
    const humidityValue = prompt("  :");
    if (humidityValue) {
      alert(` : ${humidityValue}`);
    }
  };

  const setSoilMoisture = () => {
    const moistureValue = prompt("   :");
    if (moistureValue) {
      alert(`  : ${moistureValue}`);
    }
  };

  return (
    <div className="controls">
      <input type="number" id="timeInput" placeholder=" " />
      <select id="timeUnit">
        <option value="seconds"></option>
        <option value="minutes"></option>
        <option value="hours"></option>
      </select>
      <button>  </button>
      <button onClick={setTemperature}> </button>
      <button onClick={setHumidity}> </button>
      <button onClick={setSoilMoisture}>  </button>
    </div>
  );
}

export default Controls;
